.button {
  font-family: 'Open Sans';
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  border: 2px solid #0A2640;
  border-radius: 24px;
  color: #0A2640;
  padding: 8px 40px;
  background-color: transparent;
  cursor: pointer; }
  .button:hover {
    content: "";
    transition: color ease 0.35s, transform ease 0.35s;
    transform: scale(1.1);
    color: #65E4A3; }
  .button.button {
    margin-right: 20px; }
  .button.button:last-child {
    margin-right: 0px; }
  .button.lg {
    padding: 16px 56px;
    font-size: 20px;
    line-height: 28px;
    border-radius: 56px; }
    .button.lg.button:last-child {
      margin-right: 0; }
    .button.lg.dark {
      background-color: #0A2640;
      color: #ffffff; }
    .button.lg.dark:hover {
      color: #65E4A3; }

* {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  min-width: 320px;
  background-color: #ffffff;
  font-size: 16px; }

li {
  list-style: none; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden; }

.container {
  width: 100%;
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto; }

ul {
  margin: 0;
  padding: 0; }

.container {
  position: relative; }

.header {
  padding-top: 56px;
  position: fixed;
  width: 100%;
  z-index: 10;
  background-color: #f1f1f1;
  opacity: 0.8; }
  .header-inner {
    width: 1200px;
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
  .header-logo {
    height: 42px; }
    .header-logo img {
      height: 100%;
      width: auto; }
    .header-logo a:hover {
      content: "";
      opacity: 0.75; }
  .header-nav {
    display: flex;
    max-width: 419px;
    margin-left: auto; }
  .header-button {
    padding-left: 20px; }

.menu {
  display: flex;
  margin-left: auto;
  width: 420px;
  justify-content: flex-end; }
  .menu-item {
    display: flex;
    flex-wrap: wrap;
    padding: 0 20px; }
  .menu a {
    color: #0A2640;
    font-family: "Open Sans";
    font-weight: 600;
    font-size: 16px;
    line-height: 28px;
    transition: color ease 0.35s; }
    .menu a:hover {
      color: #65E4A3; }

.footer {
  margin-top: auto;
  padding: 40px; }

.hero-section {
  position: relative;
  width: 100%;
  padding-top: 154px; }
  .hero-section-inner {
    width: 1200px;
    display: flex; }
  .hero-section-box {
    padding-top: 64px;
    width: 601px; }
  .hero-section-heading {
    font-family: "Manrope";
    font-weight: 400;
    font-size: 48px;
    line-height: 72px;
    color: #000000;
    padding-bottom: 16px; }
  .hero-section-text {
    font-family: "Open Sans";
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    color: #777777;
    margin-bottom: 55px; }
  .hero-section-buttons {
    display: flex;
    align-items: left; }
  .hero-section::before {
    content: "";
    display: inline-block;
    width: 1293px;
    height: 1293px;
    border-radius: 50%;
    background-color: #F1F1F1;
    position: absolute;
    top: -789px;
    right: -695px; }
  .hero-section-media {
    padding-left: 105px;
    width: 494px; }

.brands {
  padding: 96px 0 82px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -28px; }
  .brands-logo {
    width: calc(100% / 6);
    text-align: center;
    height: 42px;
    padding: 0 28px; }
  .brands-logo img {
    max-width: 100%; }
